export default (client) => ({
    products: {
        all () {
            return client({
                method: 'get',
                url: '/products'
            })
        },
    
        filter(url) {
            var limit = 100
            if (url) { limit = url.searchParams.get('limit') }
            // var offset = url.searchParams.get('offset')
            return client({
                method: 'get',
                url: `/products?limit=${limit}`
            })
        },
    },

    cart: {
        all() {
            return client({
                method: 'get',
                url: `/cart`
            })
        },

        add(product, params) {
            return client({
                method: 'post',
                url: `/cart/add`,
                data: Object.assign({ product: product.id }, params)
            })
        }
    },
    
    lists: {
        all() {
            return client({
                method: 'get',
                url: `/wishlists`
            })
        },
        
        add(product, wishlist) {
            return client({
                method: 'post',
                url: `/wishlists/${ wishlist.id }/add`,
                data: { product: product.id }
            })
        },

        like(product) {
            return client({
                method: 'post',
                url: `/products/${ product.id }/like`
            })
        }
    }
})
