<template>
  <base-banner>

    <template>
      <div id="actions" class="ml-auto normalize-links">
        <router-link :to="{ name: 'home', params: { lang: $i18n.locale } }">Blog</router-link>
        <router-link :to="{ name: 'home', params: { lang: $i18n.locale } }">Help</router-link>
        <b-link v-b-modal.select-language>{{ $i18n.locale.toUpperCase() }}</b-link>

        <b-modal id="select-language" title="Select language" hide-footer>
          <b-container>
            <v-row>
              <v-col cols="12">
                <v-btn v-for="language in ['fr', 'en', 'es']" :key="language" size="lg" text @click="changeLanguage(language)">
                  <v-icon class="mr-2">mdi-france</v-icon>
                  {{ language }}
                </v-btn>
              </v-col>
            </v-row>
          </b-container>
        </b-modal>
      </div>
    </template>
    
  </base-banner>
</template>

<script>
import BaseBanner from './BaseBanner'

export default {
    name: 'BaseTopBanner',
    components: { BaseBanner },
    methods: {
      changeLanguage(language) {
        this.$router.push({ name: 'home', params: { lang: language } })
      }
    }
}
</script>
